.content {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	position: relative;
	padding-bottom: 50px;
	justify-content: space-around;
}

.Category {
	margin-top: 20px;
}

.category_header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.sortBy {
	cursor: pointer;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}

.b {
	font-weight: bold;
}

.b2 {
	font-weight: 400;
	text-decoration: underline;
}

.SortPopup {
	position: absolute;
	width: 132px;
	height: 140px;
	right: 30px;

	background-color: #F9F9F9;
	box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
	border-radius: 20px;
	z-index: 1;
	padding: 13px 0;
}

.by {
	transition: background-color 0.1s linear;
	width: 100%;
	height: 38px;
	text-align: center;
	font-size: 17px;
	font-weight: 400;
	padding-top: 8px;
}

.by:hover {
	background: #dceaf7;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	color: #3e85b8;
	;
}